
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
ViewBox
},
data() {
return {
people:'',
departmentname:'',
partyBranch:'',
total:'',
};
},
methods: {
submit(item){

this.$router.push({
path: '/party/partyPointDetail/:userid/:partyBranch',
name: 'partyPointDetail',

params: {
userid:item.id
}
})


},
getParams() {

let departmentId = this.$route.params.departmentid;


axios({
method: 'get',
url: 'pdepartment/queryById',
params: {
departmentid:departmentId
}


}) .then((res)=> {
this.departmentname=res.data.departmentname;
this.partyBranch=res.data.partyBranch;
this.people=res.data.people;

}).catch(function (error) {
console.log(error);
})


},
getParams1() {

let departmentId = this.$route.params.departmentid;
axios({
method: 'get',
url: 'ppartymember/queryByDepartmentId',
params: {
departmentid:departmentId
}

}) .then((res)=> {
this.total=res.data

}).catch(function (error) {
console.log(error);
})
}
},mounted() {
this.getParams();
this.getParams1()
}


};



